.about {
  padding-bottom: 48px;
}

.about__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.picture {
  height: 420px;
}

.about__info {
  font-size: 24px;
}

.info__name {
  color: blue;
  font-size: 32px;
}

@media only screen and (max-width: 720px) {
  .about__top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .picture {
    height: 280px;
  }
  .about__info {
    font-size: 20px;
  }
  .info__name {
    font-size: 26px;
  }
}
