.work-card{
    padding: 8px ;
    width: 85%;
    margin-top: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row-reverse;
}
.work-logo{
    height: 80px;
}
.work-info{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 18px;
}
.work-dates{
    color: gray;
}
.work-description{
    text-align: left;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: horizontal;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
}
@media only screen and (max-width: 720px) {
    .work-card{
        width: 100%;
    }
}